.item {
    display: flex;
    height: 70px;
    background-color: white;
    margin: 10px 0 10px 0;
    align-items: center;
    border-radius: 10px;
    box-shadow: 0 0 10px 5px rgba(221, 221, 221, 1);
}
.avatar {
    width: 50px;
    height: 50px;
    margin-right: 15px;
}
.status {
    width: 20px;    
    height: 20px;
    border-radius: 50%;
    background-color: aqua;
    margin: 0 15px 0 15px;
}
.name {
    margin: 0;
}